import { Injectable } from '@angular/core';
import { AngularFireDatabase,AngularFireList  } from "angularfire2/database";
@Injectable({
  providedIn: 'root'
})
export class ContactService {
 visitorList : AngularFireList<any>;
contactList : AngularFireList<any>
  constructor(private firbase : AngularFireDatabase) { }

  getContacts(){
    this.contactList = this.firbase.list('contact');
   this.visitorList = this.firbase.list('contact');
    return this.visitorList.snapshotChanges();
  }

  insertContact(contact){
    this.contactList.push({
      name : contact.yourName,
      subject : contact.subject,
      email : contact.email,
      message : contact.comment
    });    
  }
}
